.page__body {
  margin: 0 auto;
  max-width: 1920px;

  font-family: $font-default;
  font-size: $baseFontSize;
  font-weight: 400;
  line-height: 22px;
  //background-image: linear-gradient(177deg, #21264c 63%, #125a9f 87%, #0a7bd2 103%, #0689e7 142%);
  //background-size: cover;
  //background-repeat: no-repeat;
}

.page__headline {
  display: flex;
  align-items: center;

  text-transform: uppercase;
  line-height: 30px;
  color: $color-orange;

  order: -1;

  &::after {
    content: "";

    display: block;
    margin-left: 15px;
    width: 73px;
    height: 2px;

    background-color: $color-orange;
  }
}

.page__read-more {
  display: flex;
  align-items: center;
  margin-top: 18px;
  height: 42px;

  text-transform: uppercase;
  color: $color-orange;

  &::after {
    content: "";

    display: block;
    margin-left: 16px;
    width: 24px;
    height: 24px;
    color: red;

    background: url("../img/icon-arr-right.svg");
  }

  &_bg {
    padding: 8px 2px 8px 8px;
    max-width: 170px;

    font-size: $baseFontSize;
    color: $color-white-gray;

    background: rgba(237, 244, 249, 0.2);

    &::after {
      display: none;
    }
  }
}

.page__icon-more {
  margin-left: 6px;
}

.page__container {
  text-align: center;


  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
}

.page__logo-link {
  display: inline-block;
  padding: 15px 35px 6px;

  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: $color-white;

  @media (min-width: $desktop-large-width) {
    padding: 18px 44px 12px;
  }

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.4);
    outline: none;
  }
}

.page__logo-image {
  @media (min-width: $desktop-large-width) {
    width: 174px;
  }
}

.page__main-block {
  position: relative;

  margin-bottom: 48px;

  background: url("../img/bg-flag.svg") no-repeat center 32px;

  z-index: 11;

  @media (min-width: $desktop-width) {
    align-self: flex-start;

    background-position-y: -25px;
    background-size: 682px;
  }

  @media (min-width: $desktop-middle-width) {
    margin-bottom: 0;

    background-position-y: -4px;
    background-size: 680px;
  }

  @media (min-width: $desktop-large-width) {
    background-position-y: 0;
    background-size: 850px;
  }
}

.page__title {
  margin-top: 86px;
  margin-bottom: 24px;

  font-size: 45px;
  font-style: italic;
  font-weight: 800;
  line-height: 50px;
  text-transform: uppercase;
  color: $color-orange;

  @media (min-width: $desktop-width) {
    margin-top: 54px;
    margin-bottom: 34px;
    max-width: 520px;

    font-size: 57px;
    line-height: 64px;
  }

  @media (min-width: $desktop-middle-width) {
    margin-top: 80px;

    font-size: 57px;
    line-height: 63px;
  }

  @media (min-width: $desktop-large-width) {
    margin-top: 100px;
    margin-bottom: 24px;
    max-width: 680px;

    font-size: 72px;
    line-height: 80px;
  }

  span {
    display: block;
    margin-top: 4px;

    font-size: 30px;
    line-height: 36px;
    color: $color-white;

    @media (min-width: $desktop-width) {
      margin-top: 8px;

      font-size: 37px;
      line-height: 45px;
    }

    @media (min-width: $desktop-middle-width) {
      font-size: 38px;
      line-height: 46px;
    }

    @media (min-width: $desktop-large-width) {
      font-size: 47px;
      line-height: 57px;
    }
  }
}

.page__spec-title {
  margin-bottom: 50px;

  font-size: 18px;
  font-weight: normal;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $desktop-width) {
    margin-bottom: 18px;
  }

  @media (min-width: $desktop-large-width) {
    margin-bottom: 68px;
  }
}

.page__description {
  margin: 0 auto;
  max-width: 320px;

  font-size: 16px;
  font-style: italic;
  line-height: 22px;

  @media (min-width: $desktop-large-width) {
    font-size: 16px;
    line-height: 22px;
  }

  @media (min-width: $desktop-large-width) {
    max-width: 440px;

    font-size: 27px;
    line-height: 37px;
  }
}

.page__filter {
  position: relative;

  margin: 40px auto;
  width: 276px;

  color: $color-black;

  @media (min-width: $desktop-width) {
    margin: 16px auto;
  }

  @media (min-width: $desktop-middle-width) {
    margin-top: 30px;
  }

  @media (min-width: $desktop-large-width) {
    margin-top: 66px;
  }
}

.page__filter-selected {
  display: flex;
  align-items: center;
  padding: 16px;

  color: $basic-grey;
  border-radius: 5px;
  background-color: $color-white;

  cursor: pointer;

  @media (min-width: $desktop-width) {
    margin-bottom: 130px;
    padding: 12px 16px;
  }

  &::after {
    content: "";
    display: block;
    margin-left: auto;
    width: 0;
    height: 0;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 20px solid $deep-blue;
  }

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.4);
    outline: none;
  }
}

.page__filter-list {
  position: absolute;
  top: 48px;
  right: 0;
  left: 0;

  //todo display filter
  display: none;
  margin: 0;
  padding: 0;

  text-align: left;

  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: $color-white;

  list-style: none;
  z-index: 11;

  @media (min-width: $desktop-width) {
    top: 40px;
  }
}

.filter_open {
  & + .page__filter-list {
    display: block;
  }
}

.page__filter-item {
  padding: 4px 16px;

  border-top: 0.8px solid $color-silver;

  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}

.page__spinner {
  @media (min-width: $desktop-width) {
    margin-right: 64px;
  }

  @media (min-width: $desktop-middle-width) {
    margin-top: 76px;
    margin-right: 14px;
  }

  @media (min-width: $desktop-large-width) {
    margin-top: 76px;
    margin-right: 156px;
  }
}

.page__footer {
  margin: 48px auto;
}
