.page-footer {
  display: flex;
  flex-direction: column;
  padding: 74px 0;

  color: $color-alto;

  background-color: $color-dunkel-blau;

  a {
    color: inherit;
  }
}

.page-footer__body {
  display: flex;
  align-items: flex-start;

  dt {
    text-transform: uppercase;
  }
}

.page-footer__logo {
  margin-left: 10px;
}

.page-footer__nav {
  display: flex;
  margin-left: 7.5%;

  ul {
    margin: 0;
    padding: 0;

    list-style: none;

    &:last-child {
      margin-left: 90px;
    }
  }

  li {
    margin-bottom: 10px;
  }
}

.page-footer__phone {
  margin: 0 0 0 7.5%;

  dd {
    margin: 10px 0 0;

    font-size: $baseFontSize + 2;
    color: $color-white-gray;
  }
}

.page-footer__email {
  margin: 0 0 0 5%;

  dd {
    margin: 10px 0 0;

    font-size: $baseFontSize + 2;
    color: $color-orange;
  }
}

.page-footer__address {
  margin: 0 0 0 5.5%;
  max-width: 190px;

  dd {
    margin: 10px 0 0;

    font-size: $baseFontSize + 2;
    color: $color-white-gray;
  }
}

.page-footer__social {
  margin-right: 58px;
  margin-left: auto;
}

.page-footer__copyright {
  margin: 56px auto 0;
}
