.button {
  position: relative;

  display: flex;
  align-items: center;
  padding: 24px 24px 24px 46px;
  width: 394px;

  font-size: 20px;
  line-height: 24px;
  color: $color-white;

  background-color: $color-orange;
  box-shadow: 0 15px 30px rgba(233, 189, 148, 0.182332), 0 8px 40px rgba(233, 189, 148, 0.166149), 0 2px 20px rgba(233, 189, 148, 0.127999);

  &::after {
    content: "";

    display: block;
    margin-left: 42px;
    width: 7.5px;
    height: 12.5px;

    background: url("../img/icon-arr-button.svg") no-repeat center;
  }

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.4);
    outline: none;
  }
}

.button-close {
  position: absolute;
  top: 8px;
  right: 8px;

  padding: 5px;

  color: $button-close;

  border: none;
  background-color: transparent;
  outline: none;

  z-index: 10;
}

.button-icon {
  display: block;
}
