.social_page-header {
  .social__text  {
    -webkit-writing-mode: vertical-rl;
    writing-mode: tb-rl;

    margin-bottom: 44px;
  }

  .social__link {
    display: block;
    margin-bottom: 32px;
  }
}

.social_page-footer {
  .social__text {
    text-transform: uppercase;
  }
}

.social__icons {
  display: flex;
  margin-top: 10px;

  a {
    margin-right: 28px;

    &:last-child {
      margin-right: 0;
    }
  }
}
