.steps-work {
  margin-bottom: 100px;
}

.steps-work__head {
  display: flex;
  flex-direction: column;
  margin-left: 238px;
}

.steps-work__title {
  margin-bottom: 40px;
}

.steps-work__list {
  display: flex;
  margin: 0 0 0 238px;
  padding: 0;
  list-style: none;
}

.steps-work__item {
  position: relative;

  margin-right: 80px;
  padding-left: 62px;
  width: 234px;

  font-family: $font-second;
  font-size: $baseFontSize + 4;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;

    display: block;
    width: 32px;
    height: 32px;

    background: url("../img/icon-steps-1.svg") no-repeat;
  }

  &:nth-child(2):before {
    background-image: url("../img/icon-steps-2.svg");
  }

  &:nth-child(3):before {
    background-image: url("../img/icon-steps-3.svg");
  }

  &:nth-child(4):before {
    background-image: url("../img/icon-steps-4.svg");
  }

  &:nth-child(5):before {
    background-image: url("../img/icon-steps-5.svg");
  }

  &:not(:last-child):after {
    content: "";
    position: absolute;
    top: 30px;
    right: -46px;

    display: block;
    width: 27px;
    height: 16px;

    background: url("../img/icon-arr-steps.svg") no-repeat;
  }

  &:last-child {
    margin-right: 0;
  }

  b {
    display: block;

    font-family: inherit;
    font-size: 38px;
    font-weight: normal;
    line-height: 50px;
    text-transform: uppercase;
    color: $color-dark-blue-14;
  }
}
