.services {
  margin-bottom: 100px;
}

.services__head {
  display: flex;
  flex-direction: column;
  margin-left: 238px;
}

.services__title {
  margin-bottom: 50px;
}

.services__slider  {
  position: relative;

  margin: 0 30px;
}

.services__slider-wrapper {
  display: flex;
}

.services__slider-item {
  position: relative;

  flex-grow: 1;
  width: 372px;
  height: 808px;

  &::before {
    content: "";
    position: absolute;
    bottom: 97px;
    left: 58px;

    width: 167px;
    height: 1px;

    background-color: $color-white-gray;

    z-index: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 100%;
    background-color: $color-dark-blue-9;
    opacity: 0.4;
  }
}

.services__slide {
  position: absolute;
  top: 0;
  bottom: 0;
  object-fit: cover;
  object-position: top;

  width: 100%;
  height: 100%;
}

.services__slide-body {
  position: relative;

  padding: 97px 16px 10px 58px;
  max-width: 300px;

  font-family: $font-second;
  font-size: $baseFontSize + 4;
  line-height: 30px;
  color: $color-white;

  z-index: 1;
}

.services__slide-burger {
  position: relative;

  display: block;
  margin-top: 30px;
  width: 40px;
  height: 24px;

  border: none;
  background-color: transparent;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 40px;
    height: 2px;

    background-color: $color-white-gray;
    box-shadow: 0 10px 0 0 $color-white-gray,0 20px 0 0 $color-white-gray;
  }
}

.services__slider-nav-preview {
  position: absolute;
  top: 50%;
  top: calc(50% - 35px);
  left: 0;

  padding: 0;
  width: 84px;
  height: 70px;

  color: $color-white-gray;

  border: none;
  background-color: rgba(237, 244, 249, 0.5);
}

.services__slider-nav-next {
  position: absolute;
  top: 50%;
  top: calc(50% - 35px);
  right: 0;

  padding: 0;
  width: 84px;
  height: 70px;

  color: $color-orange;

  border: none;
  background-color: rgba(250, 250, 250, 0.5);

  transform: rotate(180deg);
}
