.page-header {
  position: relative;

  color: $color-white;
}

.page-header__wrapper {
  margin: 0 auto;
  padding: 27px 22px 116px;
  height: 933px;

  background: linear-gradient(358.96deg, rgba(58, 86, 116, 0.17) 0.4%, rgba(58, 86, 116, 0) 73.54%),
    linear-gradient(106.77deg, rgba(58, 86, 116, 0.83) -1.6%, rgba(58, 86, 116, 0) 100%),
    linear-gradient(90deg, rgba(58, 86, 116, 0.4) 0%, rgba(58, 86, 116, 0.4) 96.17%),
    url("../img/bg-head.jpg") no-repeat top;
  background-size: cover;
  background-size: contain;
}

.page-header__top {
  display: flex;
  //justify-content: space-between;
  align-items: center;
  padding-bottom: 30px;

  font-family: $font-default;

  border-bottom: 1px solid rgba(213, 219, 224, 0.3);
}

.page-header__logo-link {
  margin-right: 57px;
  margin-left: 14px;
}

.page-header__contacts {
  margin: 0 auto;
}

.page-header__phone {
  margin-right: 60px;

  font-size: $baseFontSize + 2;
  color: $color-white;
}

.page-header__email {
  font-size: $baseFontSize + 2;
  color: $color-white;
}

.page-header__calc {
  margin-left: 57px;
  padding: 3px 38px;

  font-size: $baseFontSize + 2;
  line-height: 40px;
  color: $color-white;

  border: 2px solid $color-orange;
}

.page-header__call {
  position: absolute;
  right: 154px;
  bottom: 115px;

  padding: 22px;

  border: none;
  background-color: $color-orange;
}

.page-header__social {
  position: absolute;
  top: 154px;
  left: 32px;

  font-size: $baseFontSize + 2;
}

.page-header__body {
  display: flex;
  flex-direction: column;
  margin-top: 37px;
  margin-left: 220px;
  max-width: 708px;

  &::before {
    content: "";

    margin-bottom: 32px;
    width: 32px;
    height: 40px;

    background: url("../img/icon-bookmark.svg") no-repeat center;

    order: -2;
  }

  h1 {
    margin-top: 20px;
    margin-bottom: 6px;
  }

  p {
    margin-bottom: 62px;
    max-width: 450px;

    font-size: $baseFontSize + 4;
    line-height: 28px;
  }
}
