.share {
  margin: 0;
  padding: 0;
}

.share__list {
  display: flex;
  justify-content: center;

  list-style: none;

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
    margin-right: 30px;
  }
}

.share__list_present {
  justify-content: center;
  margin-right: 0;
}

.share__item:not(:first-child) {
  margin-left: 4px;
}

.share__item_vk .share__link {
  background-color: #455c81;
}

.share__item_fb .share__link {
  background-color: #414f91;
}

.share__item_ok .share__link {
  background-color: #f0931d;
}

.share__item_tw .share__link {
  background-color: #4aa2d7;
}

.share__item_tg .share__link {
  background-color: #27aed6;
}

.share__item_wa .share__link {
  background-color: #39aa44;
}

.share__item_vb .share__link {
  background-color: #7d519e;
}

.share__link {
  display: block;

  border-radius: 50%;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.4);
    outline: none;
  }
}

.share__icon {
  display: block;
  fill: $color-white;
}
