.form {
  display: block;
  margin: 16px auto;
  width: 100%;

  &_consultation {
    display: flex;
    flex-direction: column;

    .form__list {
      display: flex;
      justify-content: center;
    }

    .form__item {
      margin-right: 20px;
      width: 100%;
      max-width: 268px;

      &:last-child {
        margin-right: 0;
      }

      &_btn {
        max-width: 290px;
      }
    }
  }
}

.form__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.form__item {
  position: relative;
  display: flex;

  margin-bottom: 20px;

  &_btn {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 24px;
      right: 24px;

      display: block;
      width: 7.5px;
      height: 12.5px;

      background: url("../img/icon-arr-button.svg") no-repeat center;
    }
  }

  label {
    cursor: pointer;
  }

  &.important {
    &::after {
      content: "*";
      position: absolute;
      top: 14px;
      right: 8px;

      font-size: 20px;
      color: red;
    }
  }

  input {
    &:disabled + label {
      color: $color-orange;

      cursor: default;
    }
  }
}

.form__field {
  position:  relative;

  padding: 10px;
  width: 100%;
  height: 60px;

  font-size: $baseFontSize + 2;
  letter-spacing: 0.02em;
  color: $color-light-blue;

  outline: none;
  border: 1px solid $color-orange;
  background: $color-white-gray;

  &:focus {
    &::placeholder {
      color: transparent;
    }

    & + .form__sub-text {
      display: block;
    }
  }
}

.form__sub-text {
  position: absolute;
  top: 0;
  left: 10px;

  font-size: 12px;
  color: $color-orange;

  display: none;
}

.form__forgot-password {
  position: absolute;
  top: 100px;
  right: 35px;

  z-index: 100;
}

.form__field_file {
  display: none;
}

.form__file-upload {
  position: relative;

  display: flex;
  align-items: center;
  padding: 10px 20px 10px 18px;
  width: 100%;
  height: 60px;

  font-size: 16px;
  letter-spacing: .02em;
  color: $color-light-blue;

  border: 1px solid $color-orange;
  background: $color-white-gray;
  outline: 0;

  &::after {
    content: "";

    display: block;
    margin-left: auto;
    width: 22px;
    height: 22px;

    background: url("../img/icon-files.svg") no-repeat center;
  }
}

.form__button {
  padding: 10px 50px 10px 10px;
  height: 60px;
  max-width: 290px;

  border: none;

  cursor: pointer;
}

.form__label-law {
  display: flex;
  margin: 10px auto;

  font-size: $baseFontSize - 2;
  color: $color-white-gray;

  cursor: pointer;
}

.form__icon-check {
  margin-right: 12px;
}

.form__input-law {
  display: none;

  &:checked + {
    .form__icon-check {
      .form__icon-path {
        display: block;
      }
    }
  }
}

.form__icon-path {
  display: none;
}

.form__field-error-message {
  margin: 10px 0;

  font-weight: 700;
  color: $color-orange;
}

.form__success {
  padding: 16px;

  font-weight: bold;
  color: white;
  background-color: green;
}
