.nav__list {
  display: flex;

  margin: 0;
  padding: 0;

  font-family: $font-second;
  font-size: $baseFontSize;
  text-transform: uppercase;
  list-style: none;

  a {
    line-height: 22px;
    text-decoration: none;
    color: $color-white;

    &:hover {
      text-decoration: underline;
      color: $red;
    }
  }
}

.nav__item {
  &:not(:last-child) {
    margin-right: 48px;
  }

  &:first-child {
    margin-right: 24px;
  }
}

.nav__item_town {
  position: relative;

  display: flex;
  align-items: center;

  cursor: pointer;

  &::after {
    content: "";

    margin-left: 10px;
    padding: 12px;
    width: 10px;
    height: 10px;

    background: url("../img/icon-arr-menu.svg") no-repeat center;
  }
}

.nav__hover-sub {
  position: relative;

  cursor: pointer;

  &::after {
    content: "";

    margin-left: 10px;
    padding: 12px;
    width: 10px;
    height: 10px;

    background: url("../img/icon-arr-menu.svg") no-repeat center;
  }
}

.nav__lang-selected {
  position: relative;

  cursor: pointer;

  &::after {
    content: "";

    margin-left: 10px;
    padding: 12px;
    width: 10px;
    height: 10px;

    background: url("../img/icon-arr-menu.svg") no-repeat center;
  }

}

.nav__sub-list {
  display: none;

  font-size: $baseFontSize - 2;
}

.nav__sub-item {
  list-style: none;
}

@media (max-width: $media-breakpoint-lg) {
  .nav__list {
    display: block;
    margin-bottom: 36px;

    a {
      display: block;
      padding: 14px 20px;
    }
  }

  .nav__item {
    &:not(:last-child) {
      margin-right: 0;
    }
  }
}

@media (max-width: $media-breakpoint-md) {
  .page-header__social {
    margin-bottom: 39px;
  }
}
