.consultation {
  padding: 160px 0;
  height: 564px;

  background: linear-gradient(358.96deg, rgba(58, 86, 116, 0.17) 0.4%, rgba(58, 86, 116, 0) 73.54%), linear-gradient(106.77deg, rgba(58, 86, 116, 0.83) -1.6%, rgba(58, 86, 116, 0) 100%), linear-gradient(90deg, rgba(58, 86, 116, 0.4) 0%, rgba(58, 86, 116, 0.4) 96.17%),
    url("../img/bg-consultation.jpg") no-repeat center/cover;
}

.consultation__head {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.consultation__title {
  margin: 24px 0 10px;

  color: $color-white-gray;
}

.consultation__headline {
  &::before {
    content: "";

    display: block;
    margin-right: 15px;
    width: 73px;
    height: 2px;

    background-color: $color-orange;
  }
}
