.customer-reviews {
  padding: 54px 0 100px;

  background-color: $color-white;
}

.customer-reviews__head {
  display: flex;
  flex-direction: column;
  margin-left: 238px;
}

.customer-reviews__title {
  margin-bottom: 48px;
}

.customer-reviews__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 128px 0 238px;
  padding: 0;
}

.customer-reviews__img {
  margin-right: 21px;

  &:nth-child(5n) {
    margin-right: 0;
  }
}

