html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  font-family: $font-default;
}

html,
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  font-family: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}

h1 {
  font-family: $font-second;
  font-size: 52px;
  font-weight: normal;
  line-height: 73px;
  text-transform: uppercase;
}

h2 {
  font-family: $font-second;
  font-weight: normal;
  font-size: 38px;
  line-height: 50px;
  text-transform: uppercase;
}

h3 {
  font-family: $font-second;
  font-weight: normal;
  font-size: 28px;
  line-height: 40px;
}

h4 {
  font-family: $font-second;
  font-weight: normal;
  font-size: 18px;
}
