.works {
  margin: 0 134px 100px;
}

.works__head {
  display: flex;
  align-items: center;
}

.works__headline-left {
  display: flex;
  flex-direction: column;
  margin-left: 104px;
}

.works__preview-slide {
  display: flex;
  align-items: center;
  margin-left: auto;
  height: 70px;

  font-family: $font-second;
  font-size: $baseFontSize + 4;
  letter-spacing: 0.075em;;
  color: $color-light-blue;

  &::after {
    content: "";

    display: block;
    margin-left: 82px;
    width: 55px;
    height: 9px;

    background: url("../img/icon-arr-work-prev.svg");
  }
}

.works__preview-slide-img {
  margin-right: 32px;
}

.works__slider-body {
  display: flex;
  //width: 1234px;
}

.works__slider-for {
  padding: 44px 84px 44px 44px;
  width: 420px;
}

.works__slider {
  position: relative;

  margin-left: auto;
  width: 1231px;
}

.works__body-text {
  dt {
    text-transform: uppercase;
    color: $color-orange;

    &:not(:first-child) {
      margin-top: 30px;
    }
  }

  dd {
    margin: 0;

    font-size: $baseFontSize + 2;
    color: $color-light-blue;
  }
}

.works__body-text-c {
  font-family: $font-second;
  font-size: $baseFontSize + 4;
  color: $color-dunkel-blau;
}

.works__slider-nav {
  position: absolute;
  right: 0;
  bottom: 0;

  display: flex;
}

.works__slider-nav-preview {
  padding: 0;
  width: 210px;
  height: 70px;

  color: $color-light-blue;

  border: none;
  background-color: $color-ultra-light-gray;
}

.works__slider-nav-next {
  padding: 0;
  width: 210px;
  height: 70px;

  color: $color-orange;

  border: none;
  background-color: $color-white-gray;

  transform: rotate(180deg);
}
