.about {
  display: flex;
  margin-bottom: 100px;
}

.about__slider-for {
  margin-top: 54px;
  margin-left: 238px;
  width: 400px;
}

.about__wrapper-text {
  display: flex;
  flex-direction: column;

  color: $color-light-blue;
}

.about__title {
  margin: 20px 0;
}

.about__description {
  font-size: $baseFontSize + 2;
  line-height: 26px;

  p {
    margin: 0 0 30px;
  }
}

.about__advantages {
  align-self: flex-end;
  margin: 0 130px 0 120px;
  padding: 0;

  counter-reset: section;
}

.about__advantage {
  margin-top: 40px;
  width: 186px;

  font-family: $font-second;
  font-size: $baseFontSize + 4;
  line-height: 30px;
  list-style: none;
  color: $color-dunkel-blau;

  span {
    display: block;
    max-width: 174px;
  }

  &::after {
    counter-increment: section;
    content: counter(section);

    display: block;
    margin-top: 12px;
    padding-left: 10px;
    width: 100%;
    height: 30px;

    color: $color-orange;

    background: url("../img/icon-rectangle-0.svg") no-repeat left;
  }
}

.about__slider {
  position: relative;

  max-width: 608px;
  width: 100%;
}

.about__slider-nav {
  position: absolute;
  right: 0;
  bottom: 0;

  display: flex;
}

.about__slider-nav-preview {
  padding: 0;
  width: 210px;
  height: 70px;

  color: $color-light-blue;

  border: none;
  background-color: $color-ultra-light-gray;
}

.about__slider-nav-next {
  padding: 0;
  width: 210px;
  height: 70px;

  color: $color-orange;

  border: none;
  background-color: $color-white-gray;

  transform: rotate(180deg);
}
