.articles {
  margin-bottom: 100px;
}

.articles__head {
  display: flex;
  flex-direction: column;
  margin-left: 238px;
}

.articles__title {
  margin-bottom: 48px;
}

.articles__list {
  display: flex;
  margin: 0 30px;
  padding: 0;

  list-style: none;
}

.articles__item {
  position: relative;

  display: flex;
  flex-grow: 1;
  width: 372px;
  height: 666px;

  //background: linear-gradient(90deg, rgba(58, 86, 116, 0.4) 0%, rgba(58, 86, 116, 0.4) 100%), url("../img/bg-services-1.jpg") no-repeat;
  background: $color-ultra-light-gray;

  &::after {
    position: absolute;
    content: "";

    display: block;
    width: 100%;
    height: 100%;

    background-color: $color-dark-blue-9;
    opacity: 0.4;
  }

  &_active {
    .articles__body-title {
      margin-top: 46px;

      color: $color-dunkel-blau;
    }

    .articles__more {
      color: $color-dark-blue-9;

      border: 1px solid $color-orange;
    }

    &::after {
      display: none;
    }
  }
}

.articles__img {
  position: absolute;
  top: 0;
  bottom: 0;
  object-fit: cover;
  object-position: top;

  width: 100%;
  height: 100%;
}

.articles__body {
  position: relative;

  margin: auto 48px 36px 30px;
  max-width: 292px;

  color: $color-white;

  z-index: 1;
}

.articles__date-public {
  font-size: $baseFontSize - 2;
  letter-spacing: 0.15em;
  color: $color-orange;
}

.articles__body-title {
  margin: 18px 0;
}

.articles__body-descr {
  margin: 46px 0 56px;

  font-size: $baseFontSize + 2;
  letter-spacing: 0.02em;
  line-height: 26px;
  color: $color-light-blue;
}
