.technique-materials {
  position: relative;
  margin: 248px 0 100px;

  //background: url("../img/bg-material.png") no-repeat left top/contain,
  //radial-gradient(56.67% 182.87% at 43.33% 45.42%, #486482 17.47%, rgba(22, 45, 69, 0.1) 100%), #3A5674;
  background: radial-gradient(56.67% 182.87% at 43.33% 45.42%, #486482 17.47%, rgba(22, 45, 69, 0.1) 100%), #3A5674;

  &::before {
    content: "";
    position: absolute;
    top: 244px;
    left: 0;
    right: 0;

    width: 100%;
    height: 1px;

    background-color: $color-alto;
    opacity: 0.3;
  }
}

.technique-materials__bg-house {
  position: absolute;
  top: -114px;
  left: 0;
}

.technique-materials__wrapper {
  display: flex;
  padding: 110px 237px 82px;
  min-height: 962px;
}

.technique-materials__brands {
  display: grid;
  grid-column-gap: 212px;
  grid-row-gap: 138px;
  grid-auto-flow: row dense;
  grid-template-columns: repeat(3, 1fr);
  margin: 97px 0 0;
  padding: 0;
  height: 100%;

  list-style: none;

  z-index: 1;
}

.technique-materials__wrapper-text {
  position: relative;

  margin-left: auto;
  min-width: 500px;

  color: $color-white;

  z-index: 2;
}

.technique-materials__head {
  display: flex;
  flex-direction: column;
}

.technique-materials__title {
  margin-bottom: 44px;
}

.technique-materials__body {
  details {
    &[open] {
      & > summary {
        margin-bottom: 4px;

        color: $color-orange;
      }

      & > ul {
        margin-bottom: 30px;
        max-height: 202px;

        overflow: auto;
      }
    }
  }

  summary {
    font-family: $font-second;
    font-size: $baseFontSize + 4;

    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &:hover {
      color: $color-orange;
    }

    &::marker {
      padding-inline-start: 30px;
    }
  }

  ul {
    margin: 0;
    padding: 0 0 0 18px;

    text-transform: uppercase;
    list-style: none;
  }

  li {
    max-height: 200px;

    line-height: 30px;
  }
}
